import { Component, OnInit, Input } from '@angular/core';
import { CatalogueService } from '../catalogue.service';

@Component({
  selector: 'app-produits',
  templateUrl: './produits.component.html',
  styleUrls: ['./produits.component.css']
})
export class ProduitsComponent implements OnInit {
private produits;
selectedProduits:any[];
total: number;
showCart: boolean;
nombreArticles: number;
@Input() quantite: number;
@Input() index: number;

  constructor( private catService :CatalogueService) { }

  ngOnInit() {
    this.showCart = false;
    this.total = 0;
    this.selectedProduits = [];
    this.getProduits();
    this.quantite = 1;
    this.nombreArticles = 0;
  }

  private getProduits() {
    this.catService.getResource("/produits")
      .subscribe( data => {
        this.produits = data['_embedded'].produits;
  
      }, err => {
        console.log(err);
      })
  }

  private arrayToObject = (array) =>
   array.reduce((obj, item) => {
     obj[item.id] = item
     return obj
   }, {})
 
  private postCommande() {
    let pdtCommandes = this.arrayToObject(this.commandeProduitTosend(this.selectedProduits));
    this.catService.postResource("/commandes",pdtCommandes)
    .subscribe(() => {
      console.log('Validé')
    }, err => {
      console.log(err)
    })
  }

  private showDetails(index){
    this.produits[index].selected = true;
    this.quantite = 1;
  
  }

  private hideDetails(index){
    this.produits[index].selected = false;
  }


  private addToCart( produit: any, quantite: number){
    let commandeProduit = {
      produit: produit,
      quantite: quantite
    }
    this.selectedProduits.push({produit, quantite});
    this.total = this.total + (produit.prix*quantite);
    this.nombreArticles = this.nombreArticles + quantite;
  }

  
  private commandeProduitTosend( selectedProduits : any[]){

    let commandeProduits  = [];
    for (let sp of selectedProduits){
      let cmdPdt = { 
        id: sp.produit.id,
        produit: sp.produit,
        quantite: sp.quantite
      }

      commandeProduits.push(cmdPdt);
    }
    return commandeProduits;
  }

  private getCart() {
    this.showCart = true;
  }
}
